@font-face {
  font-family: "Avenir";
  src: url("./fonts/Avenir-Book.ttf") format("opentype"),
    url("./fonts/Avenir-Heavy.ttf") format("opentype");
}

* {
  box-sizing: border-box;
}

html {
  font-family: "Avenir", serif;
  font-weight: 300;
}

.bar {
  position: relative;
  width: 100%;
  margin: 20px 0;
}

.big_image {
  position: absolute;
  width: 19px;
  height: 19px;
  left: 26px;
  top: 25px;
}

.small_image {
  position: absolute;
  width: 11px;
  height: 11px;
  left: 13px;
  top: 15px;
}

.search {
  width: 100%;
  color: #3d4e61;
  background: #fff;
  border: 1px solid #e1e7ed;
  box-shadow: 0 1px 8px rgba(61, 78, 97, 0.1);
  border-radius: 4px;
}

.big {
  font-family: inherit;
  padding-left: 62px;
  font-size: 16px;
  line-height: 22px;
  height: 70px;
}

.small {
  font-family: inherit;
  padding-left: 33px;
  height: 42px;
  font-size: 14px;
  line-height: 19px;
}

.search:hover {
  background: #fff;
  border: 1px solid #e1e7ed;
  box-shadow: 0 3px 8px rgba(61, 78, 97, 0.2);
  border-radius: 4px;
}

.search:focus {
  background: linear-gradient(180deg, #fff 0%, #f6f6f7 100%);
  border: 1px solid #e1e7ed;
  border-radius: 3px;
  font-weight: 900;
}
